import React from 'react'
import styles from './Intro.module.scss'
import { a, useSprings } from 'react-spring'

export const IntroTitle = ({children}) => {

  const arr = children.split('')

  const to = (i) => ({
    y: '0%',
    rotateX: 0,
    delay: i * 3,
    opacity: 1,
  });
  const from = (i) => ({
    y: '100%', 
    rotateX: -90,
    opacity: 0,
  });

  const [springs, api] = useSprings(arr.length, (i) => ({
    ...to(i),
    from: from(i),
    config: {
      duration: 300,
    }
  }))

  return (
    <div className={styles.flex}>
      {springs.map((style, i) => (
        <a.div className={styles.test} key={i} style={style}>
          <a.div className={styles.title}>{arr[i]}</a.div>
        </a.div>
      ))}
    </div>
  )
}
