import React, { useEffect, useState } from 'react'
import styles from './VideoBg.module.scss'
import firstVideo from 'src/assets/videos/cloudmill-bg-1.mp4'
import secondVideo from 'src/assets/videos/cloudmill-bg-2.mp4'
import { useScroll } from 'framer-motion'

const items = [
  {text: 'Первый', num: 1},
  {text: 'Второй', num: 2},
]

export const VideoBg = ({projectsRef, expertiseRef}) => {
  const [fadeResult, setFadeResult] = useState(0)
  const [blurResult, setBlurResult] = useState(0)
  const [current, setCurrent] = useState(1)

  const fadeOut = useScroll({
    target: projectsRef,
    offset: ["start center", "start start"]
  })

  const blur = useScroll({
    target: expertiseRef,
    offset: ["start end", "start start"]
  })

  useEffect(() => {
    fadeOut.scrollYProgress.onChange(res => {
      setFadeResult(res)
    })
    blur.scrollYProgress.onChange(res => {
      setBlurResult(res)
    })
  }, [])


  return (
    <>
    <div className={styles.test}>
      <div className={styles.title}>
        Варианты:
      </div>
      <div className={styles.buttons}>
        {items.map(item => 
          <button 
            className={styles.btn} 
            key={item.num} 
            onClick={() => setCurrent(item.num)}
          >
            {item.text}
          </button>
        )}
      </div>
    </div>
    <div 
      className={styles.layout} 
      style={{opacity: 1 - fadeResult, filter: `blur(${current !== 1 ? 24 * blurResult : 0}px)`}} 
    >
      {current === 1 
        ?
          <>
            <video className={styles.video} autoPlay loop muted playsInline>
              <source src={firstVideo} type='video/mp4'></source>
            </video>
          </>
        : current === 2 
        ?
          <div className={styles.wrapper}>
            <video className={styles.video} autoPlay loop muted playsInline>
              <source src={secondVideo} type='video/mp4'></source>
            </video>
          </div>
        :
        null
      }
    </div>
    </>
  )
}
