import React from 'react'
import { Picture } from '../Picture/Picture'
import { Text } from '../Text/Text'
import { CategoryList } from '../CategoryList/CategoryList'
import styles from './Project.module.scss'

export const Project = ({ project }) => {
  return (
    <div data-sketch=''>
      <div className={styles.container}>
        <Picture fileName={project.src} folder='projects/' />
        {/* <div data-sketch-content=''>
          <div data-images='["src/assets/images/chl.jpg"]' data-displacement data-sketch-slider=''></div>
        </div>
        <video src={require(`src/assets/videos/novikov.mp4`)} loop muted data-played='false' data-sketch-video=''></video> */}
      </div>
      <div className={styles.bot}>
        <div className={styles.col}>
          <div className={styles.title}>{project.title}</div>
        </div>
        <div className={styles.col}>
          <Text>{project.description}</Text>
        </div>
      </div>
      {project.awards 
        ?
          <div className={styles.list}>
            <CategoryList items={project.awards} />
          </div>
        :
          null
      }
    </div>
  )
}
