import axios from 'axios'
import classNames from 'classnames'
import React, { useEffect, useMemo } from 'react'
import { useState } from 'react'
import { CategoryList } from '../CategoryList/CategoryList'
import { Container } from '../Container/Container'
import { CustomReveal } from '../CustomReveal/CustomReveal'
import { Project } from '../Project/Project'
import { Section } from '../Section/Section'
import styles from './Projects.module.scss'
import * as Sketch from 'src/animations/product-animation'

const categories = ['Все', 'Retail', 'FMCG', 'E-commerce', 'IT & Telecom', 'Промышленность',]

export const Projects = ({node}) => {

  const [currentActive, setCurrentActive] = useState('Все')
  const [isLoading, setIsLoading] = useState(true)
  const [data, setData] = useState([])

  useEffect(() => {
    fetchData()
  }, [])

  useEffect(() => {
    if (!isLoading) {
      // Sketch.start()
    }
  }, [isLoading])

  async function fetchData() {
    setIsLoading(true)
    try {
      const response = await axios.get("cloudmill-public/build/projects.json")
  
      setData(response.data)
    } catch (e) {
      console.log(e)
    } finally {
      setIsLoading(false)
    }
  }

  function onClick(item) {
    setCurrentActive(item)
  }


  const filteredData = useMemo(() => {
    if (currentActive === 'Все') {
      return data
    } else {
      const arr = [...data].filter(item => item.categories.includes(currentActive))

      return arr
    }
  }, [currentActive, data])

  return (
    <Section>
      <div ref={node} className={styles.projects}>
        <Container>
          <div className={styles.grid}>
            <div className={classNames(styles.item, styles.content)}>
              <div className={styles.container}>
                <CustomReveal>
                  <div className={styles.title}>Проекты <br /> и кейсы</div>
                </CustomReveal>
                <div className={styles.wrapper}>
                  <CategoryList
                    items={categories}
                    onClick={onClick}
                    currentActive={currentActive}
                  />
                </div>
              </div>
            </div>
            {filteredData.map(item =>
              <Project project={item} key={item.title} />
            )}
          </div>
        </Container>
      </div>
    </Section>
  )
}
