import { Footer } from './components/ui/Footer/Footer';
import { Header } from './components/ui/Header/Header';
import { Main } from './components/ui/Main/Main';
import 'src/components/styles/index.scss';
import 'src/components/styles/app.scss';
import { Modal } from './components/ui/Modal/Modal';
import { useEffect, useRef, useState } from 'react';
import { Scrollbar } from 'smooth-scrollbar-react';
import { initCursor } from './animations/custom-cursor';
import { useSelector } from 'react-redux';
import { ReactComponent as Cross } from 'src/assets/images/cross.svg'
import { ShowreelModal } from './components/ui/ShowreelModal/ShowreelModal';


export default function App() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isMobile, setIsMobile] = useState(false)
  const ref = useRef()

  const { isOpen } = useSelector(state => state.modals)

  useEffect(() => {
    if (isModalOpen) {
      document.body.classList.add('body--hidden');
    } else {
      document.body.classList.remove('body--hidden');
    }
  }, [isModalOpen]);

  useEffect(() => {
    if (isOpen) {
      document.body.classList.add('body--hidden');
    } else {
      document.body.classList.remove('body--hidden');
    }
  }, [isOpen]);

  useEffect(() => {
    if (window.innerWidth < 1280) {
      setIsMobile(true)
    }

    initCursor()
  }, [])

  return (
    <div className="wrapper">
      <div className="cursor-wrapper">
        <span className="outer">
        </span>
        <div className="text-wrapper">
          {isOpen
            ?
              <div className='cursor-svg'>
                <Cross />
              </div>
            :
              <>
                Play
              </>
          }
        </div>
      </div>
      <Header openModal={() => setIsModalOpen(true)} />
      <Main isMobile={isMobile} />
      <Footer />
      <Modal isActive={isModalOpen} close={() => setIsModalOpen(false)} />
      {isOpen && <ShowreelModal />}
    </div>
  );
}
